    height: 580px;
    padding-bottom: 20px;
    margin: 2% 10px 0;
    background: rgba(104, 104, 104, 0.377);
    width: 31.5%;
  }
  
  #withIMG{
    width: 120px;
    height: auto;
    margin: 5px 34% -5px;
  }
  
  #withoutIMG{
