    height: 120px;
  }
  
  .dishName{
    /* margin-left: 0; */
    height: 50px;
    text-align: center;
  }
  
  .dishRecipte{
    margin-left: 2%;
    margin-right: 2%;
    word-wrap: break-word;
    overflow-y: auto;
    height: 53%;
  }
  
  #delete{
    margin: 0 55%;
  }