.modal{
    height: 100vh;
    width: 100vw;
    background-color: rgb(0 0 0 / 0.4);
    position: fixed;
    left: 0;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    pointer-events: none;
    transition: 0.5s;
}

.modal.active{
    opacity: 1;
    pointer-events: all;
}

.modal__content{
    padding: 20px;
    border-radius: 12px;
    background-color: white;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    -webkit-transform: scale(0.4);
            transform: scale(0.4);
    transition: 0.4s all;
}

.modal__content.active{
    -webkit-transform: scale(1);
            transform: scale(1);
}

